body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}


.game {
  padding-top: 20px;
  display: flex;
  justify-content: center;
}
button.square {
  width: 100px;
  font-size: 70px;
  height: 100px;
}
.title {
  font-size: 24px;
}

.game-info {
  margin-left: 20px;
}
.game-board {
  max-width: 480px;
  max-height: 480px;
}
.home{
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.home a{
  padding-left: 20px;
  padding-right: 20px;
}
img{
  width: 300px;
  height: 310px;
}
.one{
  cursor: auto;
  width: 400px;
  height: 310px;
}